@import "../../../public/Style/breakpoints";

.footerBottom {
  background: #001a39;
  padding: 20px 0 0;

  &__body {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1164px;
    padding: 0 15px;
    margin: 0 auto;
    @include if-device(desktop) {
      width: 100%;
      padding: 0 50px;
    }
    @include if-device(tablet) {
      width: 100%;
      padding: 0 50px;
    }
    @include if-device(largephone) {
      width: 100%;
      padding: 0 50px;
      flex-wrap: wrap;
      justify-content: center;
    }
    @include if-device(phone) {
      width: 100%;
      padding: 0 20px;
      flex-wrap: wrap;
      justify-content: center;
    }

    & > p {
      color: #999999;
      margin-bottom: 20px;
      text-transform: capitalize;
      font-size: 14px;
      font-family: "Roboto", sans-serif;
      font-weight: 400;

      @include if-device(phone) {
        text-align: center;
      }

      & > span {
        color: #ffffff;
        transition: all 0.3s ease-out;
        cursor: pointer;

        &:hover {
          color: #fdc800;
        }
      }
    }

    &__right {
      display: flex;
      align-items: center;
      column-gap: 5px;
      margin-bottom: 20px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
