@import "../../../public/Style/breakpoints";

.videoModal {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999999;
  @include if-device(largephone) {
    height: 100%;
    width: 100%;
  }
  @include if-device(phone) {
    height: 100%;
    width: 100%;
  }

  &__video {
    @include if-device(largephone) {
      height: 100%;
      width: 100%;
      padding: 0 50px;
    }
    @include if-device(phone) {
      height: 100%;
      width: 100%;
    }
    &__closeBtn {
      height: 15px;
      width: 15px;
      background-color: red;
      position: absolute;
      top: 20px;
      right: 20px;
      z-index: 1;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        color: #fff;
        font-size: 12px;
      }
    }

    & > iframe {
      width: 900px;
      height: 506px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: none;
      @include if-device(tablet) {
        width: 100%;
        padding: 0 50px;
      }
      @include if-device(largephone) {
        width: 100%;
        height: 306px;
        padding: 0 50px;
      }
      @include if-device(phone) {
        width: 100%;
        height: auto;
        padding: 0 20px;
      }
    }
  }
}
